.post-feed {
    margin: -15px;
    opacity: 0;
    transition: opacity 0.3s var(--animation-base);
}

.post-feed.initialized {
    opacity: 1;
}

.post-feed .grid-item {
    width: 330px;
}

.post-feed .post {
    position: relative;
    margin: 15px;
}

.post-feed .post-title {
    font-size: 16px;
    font-weight: 500;
}

.post-feed .post-image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100%;
}

@media (max-width: 1019px) {
    .post-feed {
        margin: -5px;
    }

    .post-feed .grid-item {
        width: calc(100% / 3);
    }

    .post-feed .post {
        margin: 5px;
    }
}

@media (max-width: 1019px) {
    .post-feed {
        margin: -5px;
    }

    .post-feed .grid-item {
        width: calc(100% / 3);
    }

    .post-feed .post {
        margin: 5px;
    }
}

@media (max-width: 640px) {
    .post-feed .grid-item {
        width: calc(100% / 2);
    }
}

@media (max-width: 450px) {
    .post-feed .grid-item {
        width: 100%;
    }
}
